<template>
  <div id="player">
    <img :src="player">
  </div>
</template>

<script>
  export default {
    async created() {
      while (!this.gameStarted) {
        await this.moving();
      }
    },
    data() {
      return {
        gameStarted: false,
        player: require('@/assets/player/player-sd0000.png')
      };
    },
    methods: {
      async moving() {
        for (let i = 0; i <= 132; i++) {
          await new Promise(resolve => {
            setTimeout(() => {
              const currentNumber = ('0000' + i).substring(i.toString().length);
              this.player = require(`@/assets/player/player-sd${currentNumber}.png`);
              resolve();
            }, 50);
          });
        }

        return;
      }
    }
  };
</script>

<style lang="scss">
  #player {
    position: absolute;
    width: 14%;
    left: 44%;
    top: 30%;
  }
</style>
