<template>
  <div id="player-running">
    <img
      :src="move.src"
      v-for="move in moves"
      :key="move.id"
      :style="{ display: move.visible ? 'block' : 'none' }"
    >
  </div>
</template>

<script>
  let previousMove = {};

  export default {
    async created() {
      this.moves = new Array(396).fill(0).map((item, index) => {
        const currentNumber = ('0000' + index).substring(index.toString().length);
        return {
          id: currentNumber,
          visible: false,
          src: require(`@/assets/player-running/player-sd${currentNumber}.png`)
        };
      });

      while (!this.gameStarted) {
        await this.moving();
      }
    },
    data() {
      return {
        gameStarted: false,
        moves: []
      };
    },
    methods: {
      async moving() {
        for (let move of this.moves) {
          await new Promise(resolve => {
            setTimeout(() => {
              previousMove.visible = false;
              move.visible = true;
              resolve();
            }, 30);
          });

          previousMove = move;
        }

        return;
      }
    }
  };
</script>

<style lang="scss">
  #player-running {
    position: absolute;
    width: 59%;
    left: 0%;
    top: 24%;
  }
</style>
