<template>
  <div id="stage" :style="{ maxWidth }">
    <img src="../assets/bg.png">
    <!-- <Player></Player> -->
    <PlayerRunning1></PlayerRunning1>
  </div>
</template>

<script>
  import Player from './Player.vue';
  import PlayerRunning1 from './PlayerRunning1.vue';
  const MAX_WIDTH = 853;
  const MAX_HEIGHT = 480;

  export default {
    created() {
      this.heightResponsive();

      window.onresize = this.heightResponsive;
    },
    data() {
      return {
        maxWidth: `${MAX_WIDTH}px`
      };
    },
    components: {
      Player,
      PlayerRunning1
    },
    methods: {
      heightResponsive() {
        if (window.innerHeight < MAX_HEIGHT) {
          this.maxWidth = window.innerHeight * (MAX_WIDTH / MAX_HEIGHT) + 'px';
        } else {
          this.maxWidth = `${MAX_WIDTH}px`;
        }
      }
    }
  };
</script>

<style lang="scss">
  body {
    margin: 0;
  }

  #stage {
    width: 100%;
    max-width: 853px;
    max-height: 480px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
  }

  img {
    display: block;
    max-width: 100%;
  }
</style>
